import { Component } from '@angular/core';
//import { CartPage } from '../cart/cart';
import { Values } from '../../providers/service/values';
import { Functions } from '../../providers/service/functions';
import { NavController, NavParams, AlertController, IonicPage  } from 'ionic-angular';
import { ProductService } from '../../providers/service/product-service';
import { ViewChild } from '@angular/core';
import { Slides } from 'ionic-angular';




@IonicPage()
@Component({
  templateUrl: 'product.html',
})
export class ProductPage {

  href: any;
  status: any;
  options: any;
  product: any;
  quantity: any;
  public disableBuyNow: boolean = false;
  public disableAddToCart: boolean = false;
  public disableWishlist: boolean = false;
  
 
  constructor(public nav: NavController, public service: ProductService, params: NavParams, public values: Values, public functions: Functions, public alert: AlertController) {
  	
    this.href = params.data;
    this.options = [];
    this.quantity = "1";

  	this.service.getProduct(this.href)
    .then((results) => this.product = results);
  }

 
  getProduct(href){
    this.service.getProduct(this.href)
    .then((results) => this.product = results);
  }


  addToCart(id){
    this.disableAddToCart=true;
    var text = '{';
    var i;

    for(i = 0; i < this.options.length; i++) {
      var res = this.options[i].split(":");
      text += '"option['+res[0]+']":"'+res[1]+'",';
    }

    text += '"product_id":"' + id + '",';
    text += '"quantity":"' + this.quantity + '"}';

    var obj = JSON.parse(text);

    this.service.addToCart(obj)
    .then((results) => this.handleCart(results));
  }


  handleCart(results){
    this.disableAddToCart=false;
    if(results.success){
      this.functions.showAlert(this.values.lan.text_success, results.success);
    }
    
    else if(results.error){
      var text = "";
      for(let error in results.error.option){
        text = text + results.error.option[error] + '<br>';
      }


      let alert = this.alert.create({
        title: "ERROR",
        subTitle: text,
        buttons: ['OK']
      });
      alert.present(alert);
    }
  }

 
  addToWishlist(product_id){
    this.disableWishlist=true;
    this.service.addToWishlist(product_id)
    .then((results) => this.handleWishlist(results));
  }


  handleWishlist(results){
    this.disableWishlist=false;
    if(results.success){
      this.functions.showAlert(this.values.lan.text_success, results.success);
    }
  }


  getCart(){
      this.nav.push('CartPage');
  }
    
  
  buyNow(id){
       this.disableBuyNow=true;

        var text = '{';
        var i;
        for (i = 0; i < this.options.length; i++) {
            var res = this.options[i].split(":");
            text += '"option['+res[0]+']":"'+res[1]+'",';
        }
        text += '"product_id":"' + id + '",';
        text += '"quantity":"' + this.quantity + '"}';

        var obj = JSON.parse(text);

     this.service.addToCart(obj)
        .then((results) => this.handleResults(results));      
  }

  
  handleResults(results){
    this.disableBuyNow= false;
    if(results.error){

      var text = "";
      for(let error in results.error.option){
        text = text + results.error.option[error] + '<br>';
      }


        let alert = this.alert.create({
          title: "ERROR",
          subTitle: text,
          buttons: ['OK']
        });

        alert.present(alert);
    }

    else{

      this.nav.push('CartPage');
    }
  }

   mySlideOptions = {
    initialSlide: 1,
    loop: true,
    autoplay: 5800,
    pager: true
  }
    
}



